import React, { useState } from 'react';
import {
  Form,
  Input,
  Button,
  Radio,
  Select,
  Cascader,
  DatePicker,
  InputNumber,
  TreeSelect,
  Switch,
} from 'antd';
import { useFormik } from 'formik';
import moment from 'moment';
import { useDispatch } from 'react-redux';
import { themPhimUploadHinhAction } from '../../../../redux/actions/QuanLyPhimAction';
import { GROUPID } from '../../../../util/setting/config';

const AddNew = () => {
  const [componentSize, setComponentSize] = useState('default');
  const [imgSrc, setImgSrc] = useState('')
  const dispatch = useDispatch()

  const onFormLayoutChange = ({ size }) => {
    setComponentSize(size);
  };

  const formik = useFormik({
    initialValues:{
      tenPhim: '',
      trailer:'',
      moTa:'',
      ngayKhoiChieu:'',
      dangChieu:false,
      sapChieu:false,
      hot:false,
      danhGia:0,
      hinhAnh:{},
      // maNhom: GROUPID
    },
    onSubmit: (values)=>{
      values.maNhom = GROUPID;
      console.log({values})
      //to i tng FormData ca JSON c sn => a gtri values t formik vo formdata
      let formData = new FormData();

      for(let key in values){
        formData[key] = values[key];
        formData.append(key, values[key]);
        if(key === 'hinhAnh'){
          //formData cha d liu dng append
          //formik.hinhAnh.name: l abc.png
          //phi gi  3 tham s
          formData.append('File',values.hinhAnh,values.hinhAnh.name);
        }else{
          formData[key] = values[key];
        }
      }
      //gi api gi cc gtri formdata v BE x l
      dispatch(themPhimUploadHinhAction(formData))

      //mn consolelog xem gi tr value t formik phi dng get
      //do tnh bo mt ca FormData nn ta ch thy KQ tr v l 1 obj rng
      //mn nhn thy value ca thuc tnh th dng get  truy xut
      // console.log('formik',formData.get('tenPhim'))
    }
  })

  const handleChangeDatePicker=(value)=>{
    console.log('date picker', moment(value).format('DD/MM/YYYY'));
    //truyn gi tr ca handleChangeDatePicker vo trng initialValues  fill gtri cho ngayKhoiChieu
    let ngayKhoiChieu =moment(value).format('DD/MM/YYYY');
    formik.setFieldValue('ngayKhoiChieu',ngayKhoiChieu)
  }

  //cch viet ging redux thunk
  //s dng closures function,  function(name)(value)
  //n dc dng khi m 1 s trng input ca antd ko c thuc tnh name hay thuc tnh khc
  //truy cp vo phm vi ca mt hm bn ngoi t mt hm bn trong
  const handleChangeSwitch=(name)=>{

    //bn phi viet thm 1 hm  nhn gtri value, v nu bn truyn (name,value) cho hm trn th value s bo li
    return (value)=>{formik.setFieldValue(name,value)}
  }

  const handleChangeInputNumber=(reviewNumber)=>{
    return (value)=>{formik.setFieldValue(reviewNumber,value)}
  }

  const handleChangeFile = (e)=>{
    //ly m file t s kin event
    //ta ch chn 1 hnh u tin nn ghi thm [0]
    let file = e.target.files[0]

      //to i tng c file 
      //i tng FileReader c sn ca JS
      let reader = new FileReader();
      //c file r tr v URL
      reader.readAsDataURL(file)
      //nhn thuc tnh URL thng qa s kin onload
      //n s tr v result l load base 64
      reader.onload = (e)=>{
        // console.log(e.target.result);
        setImgSrc(e.target.result); //hnh base 64
      }
    

    //em d liu file lu vo formik
    formik.setFieldValue('hinhAnh',file)

  }

  return (
    <>
      <Form
      onSubmitCapture = {formik.handleSubmit}
        labelCol={{
          span: 4,
        }}
        wrapperCol={{
          span: 14,
        }}
        layout="horizontal"
        initialValues={{
          size: componentSize,
        }}
        onValuesChange={onFormLayoutChange}
        size={componentSize}
      >
        <Form.Item label="Form Size" name="size">
          <Radio.Group>
            <Radio.Button value="small">Small</Radio.Button>
            <Radio.Button value="default">Default</Radio.Button>
            <Radio.Button value="large">Large</Radio.Button>
          </Radio.Group>
        </Form.Item>
        <Form.Item label="Film Name">
          <Input name='tenPhim' onChange={formik.handleChange}/>
        </Form.Item>
        <Form.Item label="Trailer">
          <Input name='trailer' onChange={formik.handleChange}/>
        </Form.Item>
        <Form.Item label="Description">
          <Input name='moTa' onChange={formik.handleChange}/>
        </Form.Item>
        <Form.Item label="Showing Date">
          <DatePicker format={'DD/MM/YYYY'} onChange={handleChangeDatePicker}/>
        </Form.Item>
        <Form.Item label="Now Showing" valuePropName="checked">
          <Switch name='dangChieu' onChange={handleChangeSwitch('dangChieu')}/>
        </Form.Item>
        <Form.Item label="Coming Soon">
          <Switch  name='sapChieu' onChange={handleChangeSwitch('sapChieu')}/>
        </Form.Item>
        <Form.Item label="Hot">
          <Switch  name='hot' onChange={handleChangeSwitch('hot')}/>
        </Form.Item>
        <Form.Item label="Star Rating">
          <InputNumber onChange={handleChangeInputNumber('danhGia')} min={1} max={10}/>
        </Form.Item>
        <Form.Item label="Picture">
          <input type='file' onChange={handleChangeFile}/>
          {/* src = "data:image/png;base64, m chui" */}
          <img style={{width:200, height:200}} src={imgSrc} alt='...' ></img>
        </Form.Item>
        <Form.Item label="Action">
          <button type='submit' className='bg-blue-500 hover:bg-blue-600 text-white p-2 rounded'>Add Film</button>
        </Form.Item>
      </Form>
    </>
  );
};

export default AddNew
