import React, { Fragment, useEffect } from "react";
import { Redirect, Route } from "react-router";
import { USER_LOGIN } from "../../util/setting/config";

export default function CheckoutTemplate(props) {
  const { Component, ...restRoute } = props;
  useEffect(() => {
    window.scrollTo(0, 0);
  });

  //nu localStorage ko c th chuyn user v trang login
  if (!localStorage.getItem(USER_LOGIN)) {
    return <Redirect to="/login"></Redirect>;
  }

  return (
    <Route
      {...restRoute}
      render={(propsRoute) => {
        return (
          <Fragment>
            <Component {...propsRoute}></Component>
          </Fragment>
        );
      }}
    ></Route>
  );
}

//propsRoute
//cha location, history, map  xt tham s trn URL => props.location,...

//restRoute: nhn v 1 ci path, nu path hp l th chy hm render ra
//cha Component, exact, path,...
