import { quanLyNguoiDungService } from "../../services/QuanLyNguoiDungService";
import {
  CHUYEN_TAB,
  CLEAR_THONG_TIN_DAT_VE,
  DANG_NHAP_ACTION,
  DAT_VE,
  DISPLAY_LOADING,
  HIDE_LOADING,
  SET_CHI_TIET_PHONG_VE
} from "../const/settingConst";
import { history } from "../../App";
import { quanLyDatVeService } from "../../services/QuanLyDatVeService";
import { ThongTinDatVe } from "../../_core/models/ThongTinDatVe";
import { connection } from "../../index";
import { notificationFunction } from "../../template/HomeTemplate/Layout/Notify/Notification";

export const layChiTietPhongVeAction = (maLichChieu) => {
  // console.log("maLichChieuAction",maLichChieu)
  return async (dispatch) => {
    try {
      dispatch({
        type: DISPLAY_LOADING
      });

      const result = await quanLyDatVeService.layChiTietPhongVe(maLichChieu);
      // console.log("layChiTietPhongVeAction", result);
      if (result.status === 200) {
        dispatch({
          type: SET_CHI_TIET_PHONG_VE,
          chiTietPhongVe: result.data.content
        });
        //login thnh cng th chuyn v trang trc 
      }
      await dispatch({
        type: HIDE_LOADING
      });
    } catch (err) {
      console.log("errlayChiTietPhongVeAction", err.response.data);
      dispatch({
        type: HIDE_LOADING
      });
    }
  };
};

export const datVeAction = (thongTinDatVe) => {
  console.log("thongTinDatVeAction", thongTinDatVe);
  return async (dispatch, getState) => {
    try {
      dispatch({
        type: DISPLAY_LOADING
      });

      const result = await quanLyDatVeService.datVe(thongTinDatVe);
      console.log("datVeAction", result);
      notificationFunction("success", "Booking Ticket is successful");
      //t v thnh cng, gi api load li trang phng v
      await dispatch(layChiTietPhongVeAction(thongTinDatVe.maLichChieu));
      await dispatch({
        type: CLEAR_THONG_TIN_DAT_VE
      });

      await dispatch({
        type: HIDE_LOADING
      });

      let { taiKhoan } = getState().QuanLyNguoiDungReducer.userLogin;
      //sau khi mnh t gh thnh cng, th trang s dc load li cho cc user khc bit l mnh t r
      await connection.invoke(
        "datGheThanhCong",
        taiKhoan,
        thongTinDatVe.maLichChieu
      );

      dispatch({ type: CHUYEN_TAB });
    } catch (error) {
      notificationFunction("error", "Booking Ticket is unsuccessful");
      console.log("err", error.response.data);
      dispatch({
        type: HIDE_LOADING
      });
    }
  };
};

export const datGheAction = (ghe, maLichChieu) => {
  //getState gip ta ly d liu t cc reducer khc ging useSelector
  //do action ko c s kin useSelector nn ta dng getState ca thunk
  return async (dispatch, getState) => {
    //a thng tin gh ln reducer
    await dispatch({
      type: DAT_VE,
      gheDuocChon: ghe
    });
    //call api t backend
    let { danhSachGheDangDat } = getState().QuanLyDatVeReducer;
    let { taiKhoan } = getState().QuanLyNguoiDungReducer.userLogin;

    //chuyn mng thnh chui
    danhSachGheDangDat = JSON.stringify(danhSachGheDangDat);

    //call api ca signalR, gi ln server BE, r sau  BE m thm tr v kt qu ngm cho cc user khc thng qua connection.on() bn mc checkout
    connection.invoke("datGhe", taiKhoan, danhSachGheDangDat, maLichChieu);
  };
};
