import { ThongTinLichChieu } from "../../_core/models/ThongTinPhongVe";
import { CHANGE_TAB_POSITION, CHUYEN_TAB, CLEAR_THONG_TIN_DAT_VE, DAT_GHE, DAT_VE, SET_CHI_TIET_PHONG_VE } from "../const/settingConst";

const initialState = {
  chiTietPhongVe:new ThongTinLichChieu(),
  danhSachGheDangDat:[],
  tabActive: 1,
  danhSachGheKhachDat:[
  ]
}

export const QuanLyDatVeReducer = (state = initialState, action) => {
  switch (action.type) {

  case SET_CHI_TIET_PHONG_VE:
    state.chiTietPhongVe = action.chiTietPhongVe;
    return { ...state };
  case DAT_VE:
    //update danh sch gh ang t
    let danhSachGheCapNhat = [...state.danhSachGheDangDat];
    let index = danhSachGheCapNhat.findIndex(item => item.maGhe === action.gheDuocChon.maGhe)
    //nu tm thy index
    //iu ny khin cho mng danhSachGheDangDat s ko c s gh trng nhau
    if(index !== -1) {
      // state.danhSachGheDangDat = danhSachGheCapNhat.filter(item => item.maGhe !== action.gheDuocChon.maGhe)
    danhSachGheCapNhat.splice(index, 1);
    }else{
      danhSachGheCapNhat.push(action.gheDuocChon);
    }
    return { ...state, danhSachGheDangDat:danhSachGheCapNhat };

    case CLEAR_THONG_TIN_DAT_VE:
    state.danhSachGheDangDat = [];
    return { ...state}

    case CHUYEN_TAB:
    state.tabActive = 2;
    return{...state}

    case CHANGE_TAB_POSITION:
    state.tabActive = action.number;
    return{...state}

    case DAT_GHE:
      state.danhSachGheKhachDat = action.arrGheKhachDat;
      return{...state}


  default:
    return state
  }
};
